.input__container {
    --border-radius: 3px;
    --border: 3px;
    --clear-width: 35px
}

.input__container {
    position: relative;
    display: inline-flex;
}

.input {
    min-width: 300px; 
    padding: 10px 15px;
    padding-right: calc(var(--clear-width) + 15px);
    border: 3px solid;
    border-color: var(--color-lightgrey);
    border-radius: var(--border-radius-small);
    font-size: var(--font-size-medium);
    font-family: var(--font-family-main);
    outline: none;
}

.input:focus {
    border-color: var(--color-blue);
}

.clear__icon {
    position: absolute;

    top: var(--border);
    right: var(--border);
    width: var(--clear-width);
    height: calc(100% - var(--border)* 2);

    padding: 10px;
    object-fit: contain;
    object-position: center center;
    transition: 0.3s;
    opacity: 0.5;
    cursor: pointer;
}

.clear__icon__disabled {
    opacity: 0.2;
}